@font-face {
    font-family: octicons-anchor;
    src: url(data:font/woff;charset=utf-8;base64,) format('woff');
}

#content {
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
    color: #333;
    overflow: hidden;
    font-family: "Helvetica Neue", Helvetica, "Segoe UI", Arial, freesans, sans-serif;
    font-size: 22px;
    line-height: 1.6;
    word-wrap: break-word;
}

#content a {
    background: transparent;
}

#content a:active,
#content a:hover {
    outline: 0;
}

#content strong {
    font-weight: bold;
}

#content h1 {
    font-size: 2em;
    margin: 0.67em 0;
}

#content img {
    border: 0;
}

#content hr {
    -moz-box-sizing: content-box;
    box-sizing: content-box;
    height: 0;
}

#content pre {
    overflow: auto;
}

#content code,
#content kbd,
#content pre {
    font-family: monospace, monospace;
    font-size: 1em;
}

#content input {
    color: inherit;
    font: inherit;
    margin: 0;
}

#content html input[disabled] {
    cursor: default;
}

#content input {
    line-height: normal;
}

#content input[type="checkbox"] {
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    padding: 0;
}

#content table {
    border-collapse: collapse;
    border-spacing: 0;
}

#content td,
#content th {
    padding: 0;
}

#content * {
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

#content input {
    font: 13px/1.4 Helvetica, arial, freesans, clean, sans-serif, "Segoe UI Emoji", "Segoe UI Symbol";
}

#content a {
    color: #4183c4;
    text-decoration: none;
}

#content a:hover,
#content a:focus,
#content a:active {
    text-decoration: underline;
}

#content hr {
    height: 0;
    margin: 15px 0;
    overflow: hidden;
    background: transparent;
    border: 0;
    border-bottom: 1px solid #ddd;
}

#content hr:before {
    display: table;
    content: "";
}

#content hr:after {
    display: table;
    clear: both;
    content: "";
}

#content h1,
#content h2,
#content h3,
#content h4,
#content h5,
#content h6 {
    margin-top: 15px;
    margin-bottom: 15px;
    line-height: 1.1;
}

#content h1 {
    font-size: 30px;
}

#content h2 {
    font-size: 21px;
}

#content h3 {
    font-size: 16px;
}

#content h4 {
    font-size: 14px;
}

#content h5 {
    font-size: 12px;
}

#content h6 {
    font-size: 11px;
}

#content blockquote {
    margin: 0;
}

#content ul,
#content ol {
    padding: 0;
    margin-top: 0;
    margin-bottom: 0;
}

#content ol ol,
#content ul ol {
    list-style-type: lower-roman;
}

#content ul ul ol,
#content ul ol ol,
#content ol ul ol,
#content ol ol ol {
    list-style-type: lower-alpha;
}

#content dd {
    margin-left: 0;
}

#content code {
    font: 12px Consolas, "Liberation Mono", Menlo, Courier, monospace;
}

#content pre {
    margin-top: 0;
    margin-bottom: 0;
    font: 12px Consolas, "Liberation Mono", Menlo, Courier, monospace;
}

#content kbd {
    background-color: #e7e7e7;
    background-image: -webkit-linear-gradient(#fefefe, #e7e7e7);
    background-image: linear-gradient(#fefefe, #e7e7e7);
    background-repeat: repeat-x;
    border-radius: 2px;
    border: 1px solid #cfcfcf;
    color: #000;
    padding: 3px 5px;
    line-height: 10px;
    font: 11px Consolas, "Liberation Mono", Menlo, Courier, monospace;
    display: inline-block;
}

#content>*:first-child {
    margin-top: 0 !important;
}

#content>*:last-child {
    margin-bottom: 0 !important;
}

#content .anchor {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    display: block;
    padding-right: 6px;
    padding-left: 30px;
    margin-left: -30px;
}

#content .anchor:focus {
    outline: none;
}

#content h1,
#content h2,
#content h3,
#content h4,
#content h5,
#content h6 {
    position: relative;
    margin-top: 1em;
    margin-bottom: 16px;
    font-weight: bold;
    line-height: 1.4;
}

#content h1 .octicon-link,
#content h2 .octicon-link,
#content h3 .octicon-link,
#content h4 .octicon-link,
#content h5 .octicon-link,
#content h6 .octicon-link {
    display: none;
    color: #000;
    vertical-align: middle;
}

#content h1:hover .anchor,
#content h2:hover .anchor,
#content h3:hover .anchor,
#content h4:hover .anchor,
#content h5:hover .anchor,
#content h6:hover .anchor {
    height: 1em;
    padding-left: 8px;
    margin-left: -30px;
    line-height: 1;
    text-decoration: none;
}

#content h1:hover .anchor .octicon-link,
#content h2:hover .anchor .octicon-link,
#content h3:hover .anchor .octicon-link,
#content h4:hover .anchor .octicon-link,
#content h5:hover .anchor .octicon-link,
#content h6:hover .anchor .octicon-link {
    display: inline-block;
}

#content h1 {
    padding-bottom: 0.3em;
    font-size: 2.25em;
    line-height: 1.2;
    border-bottom: 1px solid #eee;
}

#content h2 {
    padding-bottom: 0.3em;
    font-size: 1.75em;
    line-height: 1.225;
    border-bottom: 1px solid #eee;
}

#content h3 {
    font-size: 1.5em;
    line-height: 1.43;
}

#content h4 {
    font-size: 1.25em;
}

#content h5 {
    font-size: 1em;
}

#content h6 {
    font-size: 1em;
    color: #777;
}

#content p,
#content blockquote,
#content ul,
#content ol,
#content dl,
#content table,
#content pre {
    margin-top: 0;
    margin-bottom: 16px;
}

#content hr {
    height: 4px;
    padding: 0;
    margin: 16px 0;
    background-color: #e7e7e7;
    border: 0 none;
}

#content ul,
#content ol {
    padding-left: 2em;
}

#content ul ul,
#content ul ol,
#content ol ol,
#content ol ul {
    margin-top: 0;
    margin-bottom: 0;
}

#content li>p {
    margin-top: 16px;
}

#content dl {
    padding: 0;
}

#content dl dt {
    padding: 0;
    margin-top: 16px;
    font-size: 1em;
    font-style: italic;
    font-weight: bold;
}

#content dl dd {
    padding: 0 16px;
    margin-bottom: 16px;
}

#content blockquote {
    padding: 0 15px;
    color: #777;
    border-left: 4px solid #ddd;
}

#content blockquote>:first-child {
    margin-top: 0;
}

#content blockquote>:last-child {
    margin-bottom: 0;
}

#content table {
    display: block;
    width: 100%;
    overflow: auto;
    word-break: normal;
    word-break: keep-all;
}

#content table th {
    font-weight: bold;
}

#content table th,
#content table td {
    padding: 6px 13px;
    border: 1px solid #ddd;
}

#content table tr {
    background-color: #fff;
    border-top: 1px solid #ccc;
}

#content table tr:nth-child(2n) {
    background-color: #f8f8f8;
}

#content img {
    max-width: 100%;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

#content code {
    padding: 0;
    padding-top: 0.2em;
    padding-bottom: 0.2em;
    margin: 0;
    font-size: 85%;
    background-color: rgba(0, 0, 0, 0.04);
    border-radius: 3px;
}

#content code:before,
#content code:after {
    letter-spacing: -0.2em;
    content: "\00a0";
}

#content pre>code {
    padding: 0;
    margin: 0;
    font-size: 100%;
    word-break: normal;
    white-space: pre;
    background: transparent;
    border: 0;
}

#content .highlight {
    margin-bottom: 16px;
}

#content .highlight pre,
#content pre {
    padding: 16px;
    overflow: auto;
    font-size: 85%;
    line-height: 1.45;
    background-color: black;
    border-radius: 3px;
}

#content .highlight pre {
    margin-bottom: 0;
    word-break: normal;
}

#content pre {
    word-wrap: normal;
}

#content pre code {
    display: inline;
    max-width: initial;
    padding: 0;
    margin: 0;
    overflow: initial;
    line-height: inherit;
    word-wrap: normal;
    background-color: transparent;
    border: 0;
}

#content pre code:before,
#content pre code:after {
    content: normal;
}

#content .highlight {
    background: #fff;
}

