}

#content .highlight .kc,
#content .highlight .kd,
#content .highlight .kn,
#content .highlight .kp,
#content .highlight .kr,
#content .highlight .kt,
#content .highlight .k,
#content .highlight .o {
    font-weight: bold;
}

#content .highlight .kt {
    color: #458;
}

#content .highlight .c,
#content .highlight .cm,
#content .highlight .c1 {
    color: #998;
    font-style: italic;
}

#content .highlight .cp,
